import React, { useState } from "react";
import { ThemeProvider, CssBaseline, AppBar, Toolbar, Typography, Tabs, Tab, Container, Box } from "@mui/material";
import SecurityIcon from "@mui/icons-material/Security";
import InfoIcon from "@mui/icons-material/Info";
import AssessmentIcon from "@mui/icons-material/Assessment";
import Inventory2Icon from "@mui/icons-material/Inventory2";
import PeopleIcon from "@mui/icons-material/People";
import WarningIcon from "@mui/icons-material/Warning";
import ReportIcon from "@mui/icons-material/Report";

import theme from "./theme";
import DashboardTab from "./components/DashboardTab";
import PredictionTab from "./components/PredictionTab";
import ResourcesTab from "./components/ResourcesTab";
import CoordinationTab from "./components/CoordinationTab";
import AlertsTab from "./components/AlertsTab";
import ReportingTab from "./components/ReportingTab";

function App() {
  const [tabValue, setTabValue] = useState(0);
  const handleTabChange = (event, newValue) => setTabValue(newValue);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      
      {/* Fixed Header */}
      <AppBar position="fixed" color="primary">
        <Toolbar>
          <SecurityIcon fontSize="large" sx={{ mr: 2 }} />
          <Typography variant="h5" sx={{ flexGrow: 1 }}>
            Disaster Management Dashboard
          </Typography>
        </Toolbar>
      </AppBar>

      {/* Add spacing for AppBar */}
      <Box sx={{ mt: 10 }}>
        <Tabs value={tabValue} onChange={handleTabChange} centered textColor="primary" indicatorColor="primary">
          <Tab label="Dashboard" icon={<InfoIcon />} />
          <Tab label="Prediction" icon={<AssessmentIcon />} />
          <Tab label="Resources" icon={<Inventory2Icon />} />
          <Tab label="Coordination" icon={<PeopleIcon />} />
          <Tab label="Alerts" icon={<WarningIcon />} />
          <Tab label="Reporting" icon={<ReportIcon />} />
        </Tabs>

        <Container maxWidth="lg" sx={{ mt: 3 }}>
          {tabValue === 0 && <DashboardTab />}
          {tabValue === 1 && <PredictionTab />}
          {tabValue === 2 && <ResourcesTab />}
          {tabValue === 3 && <CoordinationTab />}
          {tabValue === 4 && <AlertsTab />}
          {tabValue === 5 && <ReportingTab />}
        </Container>
      </Box>
    </ThemeProvider>
  );
}

export default App;
