import { useState } from "react";
import { getPrediction } from "../services/api";
 // Import API call function

const PredictComponent = () => {
  const [features, setFeatures] = useState({ feature1: "", feature2: "", feature3: "" });
  const [prediction, setPrediction] = useState(null);

  const handleChange = (e) => {
    setFeatures({ ...features, [e.target.name]: e.target.value });
  };

  const handleSubmit = async () => {
    const result = await getPrediction(features.feature1, features.feature2, features.feature3);
    setPrediction(result);
  };

  return (
    <div>
      <input type="number" name="feature1" value={features.feature1} onChange={handleChange} />
      <input type="number" name="feature2" value={features.feature2} onChange={handleChange} />
      <input type="number" name="feature3" value={features.feature3} onChange={handleChange} />
      <button onClick={handleSubmit}>Predict</button>
      {prediction && <p>Prediction: {prediction}</p>}
    </div>
  );
};

export default PredictComponent;
