import React from "react";
import { Box, Typography, List, ListItem, ListItemText, Divider } from "@mui/material";
import WarningIcon from "@mui/icons-material/Warning";

const Alerts = () => {
  const alerts = [
    { location: "Kerala", disaster: "Flood", severity: "High" },
    { location: "Gujarat", disaster: "Earthquake", severity: "Moderate" },
  ];

  return (
    <Box p={3}>
      <Typography variant="h4" gutterBottom>
        Live Disaster Alerts
      </Typography>
      <List>
        {alerts.map((alert, index) => (
          <React.Fragment key={index}>
            <ListItem>
              <WarningIcon sx={{ color: "red", mr: 2 }} />
              <ListItemText
                primary={`${alert.disaster} in ${alert.location}`}
                secondary={`Severity: ${alert.severity}`}
              />
            </ListItem>
            {index < alerts.length - 1 && <Divider />}
          </React.Fragment>
        ))}
      </List>
    </Box>
  );
};

export default Alerts;
