import axios from "axios";

const API_BASE_URL = "http://127.0.0.1:8000"; // Your FastAPI backend URL

// Function to fetch disaster data
export const getDisasterData = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/disasters`);
    return response.data;
  } catch (error) {
    console.error("Error fetching disaster data:", error);
    return null;
  }
};

// Function to send emergency request
export const sendEmergencyRequest = async (data) => {
  try {
    const response = await axios.post(`${API_BASE_URL}/emergency`, data);
    return response.data;
  } catch (error) {
    console.error("Error sending emergency request:", error);
    return null;
  }
};


export const getPrediction = async (feature1, feature2, feature3) => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/predict/?feature1=${feature1}&feature2=${feature2}&feature3=${feature3}`
    );
    return response.data.prediction;
  } catch (error) {
    console.error("Error fetching prediction:", error);
    return null;
  }
};
