import React from "react";
import { Box, Typography, List, ListItem, ListItemText, Divider } from "@mui/material";

const Resources = () => {
  const resources = [
    { name: "Food Supplies", quantity: "1500 kg", status: "Available" },
    { name: "Medical Kits", quantity: "500 units", status: "Low" },
    { name: "Rescue Boats", quantity: "20", status: "Operational" },
  ];

  return (
    <Box p={3}>
      <Typography variant="h4" gutterBottom>
        Resource Availability
      </Typography>
      <List>
        {resources.map((resource, index) => (
          <React.Fragment key={index}>
            <ListItem>
              <ListItemText
                primary={`${resource.name} - ${resource.quantity}`}
                secondary={`Status: ${resource.status}`}
              />
            </ListItem>
            {index < resources.length - 1 && <Divider />}
          </React.Fragment>
        ))}
      </List>
    </Box>
  );
};

export default Resources;
